import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)
  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/category',
    name: 'Category',
    component: () => import(/* webpackChunkName: "about" */ '../views/Category.vue')
  },
  {
    path: '/cart',
    name: 'Cart',
    component: () => import(/* webpackChunkName: "cart" */ '../views/Cart.vue')
  },
  {
    path: '/profile',
    name: 'Profile',
    component: () => import(/* webpackChunkName: "profile" */ '../views/Profile.vue')
  },
  {
    path: '/apple',
    name: 'Apple',
    component: () => import(/* webpackChunkName: "apple" */ '../views/Apple.vue')
  },
  {
    path: '/banana',
    name: 'Banana',
    component: () => import(/* webpackChunkName: "banana" */ '../views/Banana.vue')
  },
  {
    path: '/pear',
    name: 'Pear',
    component: () => import(/* webpackChunkName: "pear" */ '../views/Pear.vue')
  },
  {
    path: '/cherry',
    name: 'Cherry',
    component: () => import(/* webpackChunkName: "cherry" */ '../views/Cherry.vue')
  },
  {
    path: '/weixin',
    name: 'Weixin',
    component: () => import(/* webpackChunkName: "weixin" */ '../views/Weixin.vue')
  },
  {
    path: '/pinduoduo',
    name: 'Pd',
    component: () => import(/* webpackChunkName: "pinduoduo" */ '../views/Pd.vue')
  },
  {
    path: '/geolocation',
    name: 'Geolocation',
    component: () => import(/* webpackChunkName: "geolocation" */ '../views/Geolocation.vue')
  },
  {
    path: '/navigation',
    name: 'Navigation',
    component: () => import(/* webpackChunkName: "navigation" */ '../views/Navigation.vue')
  },
  {
    path: '/microblog',
    name: 'Microblog',
    component: () => import(/* webpackChunkName: "microblog" */ '../views/Microblog.vue')
  },
  {
    path: '/qq',
    name: 'Qq',
    component: () => import(/* webpackChunkName: "qq" */ '../views/Qq.vue')
  },
  {
    path: '/douyin',
    name: 'Dy',
    component: () => import(/* webpackChunkName: "douyin" */ '../views/Dy.vue')
  },
  {
    path: '/compass',
    name: 'Compass',
    component: () => import(/* webpackChunkName: "compass" */ '../views/Compass.vue')
  },
  {
    // path: '/detail/:id/:age/:sex', //params参数
    path: '/detail/:id',
    name: 'Detail',
    component: () => import(/* webpackChunkName: "detail" */ '../views/Detail.vue')
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  //设定路由点击的颜色，'active'是自定义高亮的名字
  linkExactActiveClass:'active',
  // linkActiveClass:'active',
  routes
})

export default router
