<template>
    <div class="carousel">
      <img v-for="(item,index) in imgArr" :key="index" :src="item" v-show="n===index">
      <div class="carousel-circle">
        <!-- 轮播点的个数是跟随图片的个数来定的， -->
        <ul>
          <li v-for="(v,i) in imgArr" :key="i" :class="{'selected':n===i}"></li>
        </ul>
      </div>
    </div>
</template>

<script>
// toRefs是响应式的数据，是一个方法
//传的时候是父组件中的provide 接收子组件中的是inject
import {reactive,toRefs,onMounted,onBeforeUnmount,inject} from '@vue/composition-api'
export default {
  components:{
  },
  //vue2.0 子组件接收
  // props:{
  //   imgArr:Array
  // },
  //setup (props,'整个vue实例，....') //第二个参数对象，可以写成解构赋值形式
  setup (props) {
    //vue 3.0接收父组件传过来的值
    // const imgArr = inject('imgArr'); //第一种写法写在state外边，需要返回
    const state = reactive({
      n:0,
      // imgArr:[
      // //webpack解析,不会添加对应的地址，需要加上require
      //   require( '../assets/images/banner-1.png'),
      //   require( '../assets/images/banner-2.png'),
      // ]
      imgArr:inject('imgArr'), //第二种写法写在state中
      timer:null,
    })
    //定时器
    const autoPlay=()=>{
      state.timer = setInterval(play,2000)
    };
    //调用方法写法
    const play =() =>{ //不停调用它
      state.n++; //让n++
      // if(state.n >= state.imgArr.length) {
      // if(state.n >= imgArr.length) {
      if(state.n >= state.imgArr.length) { //第二种写法写在状态中
        state.n = 0;//每次进来调用，当大于图片长度，重新设置为第一张图片，如此往复，无限循环
      }
    };
    onMounted (()=>{
      autoPlay();
    })
    onBeforeUnmount (()=>{ //相当于beforeDestory
      clearInterval(state.timer) //清除定时器
    })
    return {
      ...toRefs(state),
      // imgArr,//第一种写法返回传递过来的值 state状态外
    }
  }
}
// var obj = {id:1,name:2}  解构赋值 var {id:name} = obj;
</script>

<style scoped>
  .carousel {
    display:flex;
    position:relative;
    min-height:250px;
    overflow:hidden;
  }
  .carousel img {
    width:100%;
    min-height:250px;
  }
  .carousel-circle {
    width:100%;
    height:10px;
    position:absolute;
    bottom:10px;
    z-index:10000;
  }
  .carousel-circle ul {
    width:60px;
    margin:0 auto;
  }
  .carousel-circle ul>li {
    float:left;
    width:10px;
    height:10px;
    border-radius:50%;
    background:red;
    margin:0 10px;
  }
  .carousel-circle ul>.selected {
    background:blue;
  }
</style>
