<template>
    <div class="container">
      <!-- 轮播图-->
      <Carousel></Carousel>
      <!-- 导航‘九宫格’展示 -->
      <Nav></Nav>
      <!-- 图文列表 -->
      <Lists></Lists>
    </div>
</template>

<script>
// @ is an alias to /src
//react 是处理响应式的数据 
//toRefs 是处理响应式的数据 
//react 是处理响应式的数据 双向绑定
//provide 组件传参 provide('数据的名字',要传递的值)，相当于vue2.0的props
//inject 接收组件中可以在setup中这样写: const imgArr =inject('数据的名字')
//3.0的计算属性也是写在state中的
//onMounted相当于mounted
//setup 相当于data
//所有的方法和生命周期写成箭头函数的形式
// import {reactive,toRefs,computed,provide,inject,onMounted} from '@vue/composition-api'
import {reactive,toRefs,onMounted,provide} from '@vue/composition-api'
import Nav from '../components/Nav'
import Lists from '../components/Lists'
import Carousel from '../components/Carousel'
export default {
  name: 'Home',
  components:{
    Nav,
    Lists,
    Carousel
  },
  //setup里边没法访问this ******
  //setup(props,{a,b,c}) //第一个参数要传递的值，第二个参数相当于整个new Vue实例化的对象，一般使用es6的解构赋值的写法;
 
  setup (props,{root}) {
    const state = reactive ({
      // title:'hello vue3.0 !!!',
      // age: 19,
      // sex:'男',
      // num:computed(()=>100)
       imgArr:[
      //webpack解析,不会添加对应的地址，需要加上require
        require( '../assets/images/banner-1.png'),
        require( '../assets/images/banner-2.png'),
      ]
    });
    //vue2.9 父子组件传参 <Child :imgArr="imgArr"></Child>
    //vue3.0 父子组件传参，第一个是组件1名字，第二个是组件的名字,而且所有组件都可以使用，包括孙子组件
    provide('imgArr',state.imgArr); 
    //返回指定的属性
    //方法
    // let clickHandler = ()=> { 
     
    // }
    // return {
    //   title :state.title,
    //   age:state.age,
    //   clickHandler, //返回事件
    // }
    //返回全部的属性 es6的扩展运算符
    onMounted(() =>{
      document.documentElement.getElementsByTagName('body')[0].style.background="#f2f2f2"
    })
    return {
      ...toRefs(state),
    }
  }
}
</script>
<style scoped>
 
</style>


