import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
// 组件过于复杂的时候，我们可以使用vuex
//或者
const state ={
  isNavShow:false //底部导航的显示
}
const getters ={ //状态里边的计算属性
  showNav(state) {
    return state.isNavShow
  }
}
const mutations ={
  hideNav( state ){
    state.isNavShow = false;
  },
  showNav( state ){
    state.isNavShow  = true;
  }
}
const actions ={
  hideNav({commit}) {
    commit('hideNav')
  },
  showNav({commit}) {
    commit('showNav')  
  }
}

export default new Vuex.Store({
  state,
  mutations,
  getters,
  actions,
  modules: {
  }
})
