<template>
  <!-- vue3.0入门 -->
  <div id="app">
    <router-view></router-view>
    <!-- 底部导航 tabbar -->
    <!-- <Footer v-show="isNavShow"></Footer> -->
    <Footer v-if="isNavShow"></Footer>
    <!-- <Footer v-show="$store.state.isNavShow"></Footer> -->
  </div>
</template>
<script>
// @ is an alias to /src
//react 是处理响应式的数据
import {reactive,toRefs,computed,onMounted,onUnmounted} from '@vue/composition-api'
import Footer from './components/Footer'
import {mapState} from 'vuex'
//辅助函数 mapGetters
// import {mapGetters} from 'vuex'
//计算属性computed 
//可以写在state中 不需要定义变量 
//也可以写在state外边 需要定义一个变量
export default {
  // computed:mapGetters(['showNav']), //vue2.0的数组的写法
  // computed:{ //对象写法
  //   ...mapState({
  //     isNavShow:'isNavShow' //属性名是自定义的名字，属性值是$store中的state中的值
  //   })
  // },
  // computed:{
  //   isNavShow:()=>this.$store.state.isNavShow
  // },
  computed:mapState(['isNavShow']), //数组的写法
  components:{
    Footer
  },
  setup (props,{root,emit,slot}) { //root就是实例化的vue，相当于this
    const state = reactive({
      // showNav:computed(() =>root.$store.getters.showNav) //vue3.0的写法
      // showNav:computed(()=>root.$store.state.isNavShow)
    })
    // let isNavShow = computed(() =>{ //computed计算属性，需要返回，生命周期函数不需要返回
    //   return root.$store.state.isNavShow  
    // })
    onMounted (() =>{ //挂载完成，相当于mounted
     console.log('root',root)
     console.log('emit',emit)
     console.log('slot',slot)
      root.$store.dispatch('showNav') //初始化显示这个tabbar
      // root.$store.dispatch('showNav',true) //初始化显示这个tabbar
      // root.$store.commit('setIsNavShow',true);
    })
    onUnmounted (() =>{ //销毁，相当于destoryed

    })
    return {
      ...toRefs(state),
      // isNavShow
    }
  }
}
</script>
